import { BaseRequestOptions } from '@angular/http';

export class AngularReduxRequest extends BaseRequestOptions {

    public token: string;
    constructor(angularReduxOptions?: any) {

        super();


        this.token = JSON.parse(localStorage.getItem("authorizationDataIdToken"));
        this.headers.append('Content-Type', 'application/json');
        this.headers.append('Authorization', 'Bearer ' + this.token);

        if (angularReduxOptions != null) {

            for (let option in angularReduxOptions) {
                let optionValue = angularReduxOptions[option];
                this[option] = optionValue;
            }
        }
    }
}



